import * as React from 'react'
import { StaticImage } from 'gatsby-plugin-image'
import { Link , useStaticQuery, graphql} from 'gatsby'
import {
    container,
    heading,
    navLinks,
    navLinkItem,
    navLinkText,
    dark } from './layout.module.css'


const Layout = ({ pageTitle, children }) => {
     
    const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  function swapDark() {
    let doc = document.querySelector("html").classList;

     if (doc.contains("layout-module--dark--1mtSC")) {

        doc.remove("layout-module--dark--1mtSC");
         
     }
     else{
         doc.add("layout-module--dark--1mtSC");
     }
  }

  return (
    <div>
    <div className={container} >
      <title>{pageTitle} | {data.site.siteMetadata.title}</title>
      <StaticImage
        alt="php code banner"
        src="../images/banner.jpg"
      />
      
      <nav>
        <ul className={navLinks}>
          <li className={navLinkItem}><Link to="/" className={navLinkText}>Home</Link></li>
          <li className={navLinkItem}><Link to="/about" className={navLinkText}>About</Link></li>
          <li className={navLinkItem}><Link to="/blog" className={navLinkText}>Blog</Link></li>
          <li className={navLinkItem}><button onClick={()=>{swapDark() }} className={navLinkText}>Darkmode</button></li>
        </ul>
      </nav>
      <main>
        <h1 className={heading}>{pageTitle}</h1>
        {children}
      </main>
    </div>
    </div>
  )
}

export default Layout